.scroll-section-outer {
  overflow: hidden;
  background-color: black;
}

.scroll-section-inner {
  background-color: black;
  height: 100vh;
  width: 600vw;
  display: flex;
  flex-direction: row;
  position: relative;
}

.scroll-section {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.scroll-section h3 {
  color: var(--primaryTextColor);
  letter-spacing: var(--headlineMedium);
  font-size: var(--headlineMedium);
  text-transform: uppercase;
}

.scroll-section:nth-child(1),
.scroll-section:nth-child(2),
.scroll-section:nth-child(3),
.scroll-section:nth-child(4) {
  background-color: #000000;
}

.scroll-section:nth-child(6) {
  background-color: #FF5B00;
}

.vertical-scroll-section {
  height: 200vh; 
  position: relative;
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.balls {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; 
}

.ball {
  position: absolute;
  z-index: 1;
}

.ball1 {
  top: 50%;
  right: 20%;
}

.ball2 {
  top: 50%;
  left: 20%;
}

.ball3 {
  top: 46%;
  left: 590px;
}

.ball4 {
  color: #000000;
  z-index: 1;
  top: 40%;
  right: 800px;
}

.section3-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #000;
  color: #fff;
  text-align: center;
  position: relative;
  max-width: 1920px;
  max-height: 1080px;
}

.clossiom-container {
  position: relative;
  margin-top: 55px;
}

.clossiom {
  left: 10px;
  z-index: 0;
  position: relative;
}

.clossiom img {
  z-index: 0;
  margin-top: 55px;
  right: 34.25%;
  height: 400px;
  width: 550px;
}

.shadow_3 {
  z-index: 1;
  background: transparent;
  position: absolute;
  top: 80%;
  left: 55%;
  transform: translateX(-50%);
  width: 700px;
  height: 100px;
  text-align: center;
}

.shadow_3 img {
  background: transparent;
  width: 100%;
  height: 50%;
}

.parallax-container {
  display: flex;
  width: auto;
  background: transparent;
  align-items: center;
  justify-content: center;
  position: relative;
}

.girl, .dude {
  bottom: -10rem;
  left: 50%;
  transform: translate(-50%, 0);
  position: absolute;
  z-index: 2;
}

.girl img, .dude img {
  background: transparent;
  transition: transform 0.05s ease;
}

.girl img, .dude img {
  height: 500px; /* Ensure both have the same size */
  width: 400px;
}
.girl img, .dude img {
  height: 500px; /* Ensure both have the same size */
  width: 400px;
}


.top-text {
  background: transparent;
  display: flex;
  justify-content: space-between;
  width: auto;
  position: absolute;
  top: 8%;
  left: 60%;
