.contact-container {
  background-color: #FF5B00;
  height: 100vh; 
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative; 
}

.back3d {
  position: relative; 
  margin-top: 18%;
}

.back3d-image {
  width: 1000px;
  height: 200px;
 
}

.shadow-image-center {
  width: 120px;
  height: 50px;
  position: absolute;
  top: 35%; 
  left: 50%; 
  transform: translate(-50%, -50%);
  z-index: 0; 
}

.shadow-image-right {
  width: 310px;
  height: 70px;
  position: absolute;
  top: 70%; 
  right: 39%; 
  transform: translate(-50%, -50%);
  z-index: 0;
}

.shadow-image-left {
  width: 150px;
  height: 50px;
  position: absolute;
  top: 72%; 
  left: 69%; 
  transform: translate(-50%, -50%);
  z-index: 0;
}

.phone-image, .email-image, .earth-image {
  position: absolute;
  
  transform: translate(-50%, -50%);
  z-index: 1; 
}

.phone-image {
  width: 250px;
  height: 170px;
  top: 45%; 
  left: 75%; 
}

.email-image {
  width: 250px;
  height: 200px;
  top: 35%; 
  left: 30%; 
}

.earth-image {
  width: 150px;
  height: 200px;
  top: -10%; 
  left: 50%;
}



.dot {
  position: absolute;
  width: 20px; 
  top: -40px; 
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.arrow-text {
  position: absolute;
  bottom: -20px; 
  left: 50%;
  transform: translateX(-50%);
  font-size: 14px;
  color: #fff;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.phone-image:hover .arrow-text,
.email-image:hover .arrow-text,
.earth-image:hover .arrow-text {
  opacity: 1;
}

.icon-container {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none; 
}

.icon-top, .icon-bottom {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.icon-top {
  margin-top: 200px;
  gap: 500px;  
  position: absolute;
  top: 0;
  width: 100%;
}

.icon-bottom {
  margin-bottom:260px;
  gap: 900px;
  position: absolute;
  bottom: 0;
  width: 100%;
}

.icon {
  height: 3.5%;
  width: 3.5%;
  pointer-events: auto; /* Allow interactions */
}

.title-contact {
  position: absolute;
  margin-right: 30rem;
  margin-bottom: 30rem;
  font-family: 'Times New Roman', Times, serif;
  font-size: 20px;
  font-weight: 800;
  word-spacing: -3px;
}

.rightArrow{
  position: relative;
  width: 100px;
  top: -92%;
  right: 20%;
}
.rightDot{
  position: relative;
  width: 10px;
  top: -119%;
  right: 20%;
}

.phoneNumber{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  position: absolute;
  top: -28%;
  right: 9%;
  font-weight: 200;
  word-spacing: 3px;
  font-size: 20px;
}

.centerArrow{
  position: relative;
  top: -150%;
  right: 50%;
}

.centerDot{
  position: relative;
  top: -175%;
  right: 50.75%;
}

.address{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  position: absolute;
  top: -100%;
  right: 45%;
  font-weight: 200;

}

.leftArrow{
  position: absolute;
  top: -55%;
  right: 67%;
}

.leftDot{
  position: absolute;
  top: -57%;
  right: 70%;
}

.email{
  position: absolute;
  top: -57%;
  right: 72%;
  font-weight: 200;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;


}


.text-2 p {
  position: relative;
  display: inline-block;
}

.text-2 p:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  transform-origin: top right;
  transform: scaleX(1);
  transition: transform 0.5s ease;
  z-index: -1;
}

.text-2 p:hover:before {
  transform: scaleX(0);
  transform-origin: top left;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-30px);
    transform: translateX(30px);
  }
}

.icon {
  height: 3.5%;
  width: 3.5%;
  pointer-events: auto; 
  animation: float 4s ease-in-out infinite;
}

.back{
  width: 30px;
  height: 30px;
}

.back-link {
  position: relative;
  top: 80px;
  font-size: 30px;
  display: flex;
  align-items: center;
}

.back-link img {
  margin-right: 8px; /* Adjust as needed for spacing */
  vertical-align: middle; /* Ensures the icon aligns well with the text */
}

.back-link a {
  display: flex;
  align-items: center;
  text-decoration: none; /* Removes underline from the link */
  color: inherit; /* Ensures it inherits the text color */
}

