/* Section1.css */

html, body {
  height: 100%;
  margin: 0;
}

.section1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #000;
  color: #fff;
  text-align: center;
  position: relative;
  width: 100%;
  max-width: 1920px;
  max-height: 1080px;
  padding: 20px;
}

.group-all {
  position: relative;
  display: inline-block;
}

.group-all img {
  width: 100%;
  height: auto;
  max-width: 500px;
  max-height: 473px;
}

.group-chart {
  background-color: transparent;
  position: absolute;
  top: 200px;
  right: 0;
  left: 20%;
}

.group-chart img {
  max-width: 230px;
  height: auto;
}

.group-logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  margin-left: 50px;
  right: 90%;
  top: 50%;
  transform: translateY(-50%);
  width: 100px;
  cursor: pointer;
  z-index: 10; /* Added z-index */

}

.animated-letter {
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  color: #FF5B00;
  text-align: right;
  font-family: Arial, Helvetica, sans-serif;
  text-align: right;
  color: #ff5b00;
}

.text-right {
  position: absolute;
  top: 50%;
  right: 70px;
  transform: translateY(-50%);
  width: 200px;
}

.text-right p {
  font-weight: 600;
  font-size: 15px;
  color: #ff5b00;
  text-align: right;
}

.text-center {
  top: 62%;
  text-align: left;
  display: flex;
  position: absolute;
  margin-right: 330px;
  right: -2rem;
  transform: translateY(-50%);
  width: auto;
  line-height: 1.25;
}

.text-left {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background-color: transparent;
  top: 55%;
  text-align: left;
  display: flex;
  position: absolute;
  margin-left: 300px;
  left:-4rem;
  transform: translateY(-50%);
  width: auto;
  line-height: 1.25;
}

.title-text-1 {
  background: transparent;
  display: flex;
  position: absolute;
  top: 7%;
  margin-left: 260px;
  left: 0;
  text-align: left;
  font-size: 70px;
  font-weight: 800;
  line-height: 1.25;
  color: #fff;
  -webkit-text-size-adjust: none;
}

.menu-icon-1 {
  position: absolute;
  top: -5rem; 
  left: 79px; 
  width: 170px; 
  height: auto;
  cursor: pointer;
  z-index: 1000;
}

.menu-links-1 {
  z-index: 1001;
  font-size: 20px; 
  position: absolute;
  top: 50%;
  left: 8%;
  transform: translate(-50%, -50%);
}

.menu-link-1  {
  position: absolute;
  color: #000000;
  font-size: 20px;
  text-decoration: none;
  pointer-events: auto; 
}


.menu-link-1:hover {
  text-decoration: underline; 

.home-link-1 {
  top: 25px;
  left: 65px;
  z-index: 1; 
}
.group-logo{
  z-index: 1;
}
.work-link-1 {
  color: aqua;
  margin-left: 150px; 
  margin-bottom: 40px; 
  top: -17px; 
}

.about-link-1 {
  top: -60px; 
  left: 50px; 
}



@font-face {
  font-family: 'Eros';
  src: url('./fonts/west-bold.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.eros-text {
  font-family: 'Eros', sans-serif;
}}


@font-face {
  font-family: 'Inter';
  src: url('./fonts/Inter-Light.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.inter-text {
  font-family: 'Inter', sans-serif;
}