html, body {
  height: 100%;
  margin: 0;
}

.section2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #000;
  color: #fff;
  text-align: center;
  position: relative;
  width: 100%;
  max-width: 1920px;
  max-height: 1080px;
  padding: 20px;
}

.allObjectContainer {
  position: absolute; 
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.chart {
  position: absolute;
  top: 47%;
  right: calc(50% - 50px);
  transform: translateY(-50%);
  z-index: 1;
}

.chart img {
  height: 270px;
  width: auto;
}

.chart-text {
  margin-left: 10px;
  background-color: transparent;
  position: absolute;
  top: -5%;
  left: 0;
  color: white;
  font-family: 'Serif';
  font-size: 30px;
  text-align: left;
  line-height: 1.5;
}

.chart-text h1 {
  background-color: transparent;
  margin: 0;
  font-weight: bold;
  font-size:4rem;
}

.chart-text .highlight {
  color: #FF5B00;
}

.thinker {
    transition: transform 0.5s ease;

  background-color: transparent;
  position: absolute;
  top: 55%;
  left: 52%;
  transform: translate(-50%, -50%);
  z-index: 2;
  transition: transform 0.5s ease;
}

.thinker img {
  height: auto;
  background-color: transparent;
  max-width: 500px;
  max-height: 500px;
}

.fixed-object {
  background-color: transparent;
  position: absolute;
  top: 20%;
  right: 30rem;
  z-index: 2;
}

.fixed-object img {
  background-color: transparent;
  max-width: 200px;
  height: auto;
}

.Inner {
  background-color: transparent;
  position: absolute;
  top: 26%;
  right:34rem;
  z-index: 2;
}

.Inner img {
  background-color: transparent;
  max-width: 70px;
  height: auto;
}

.logo {
  position: absolute;
  top: 50%;
  left: 63px;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100px;
  z-index: 1000;
}
.text-right2 {
  top: 40%;
margin-bottom: 6rem;
  opacity: 1 !important; /* Override any opacity styles */
  transform: translateX(0) !important; /* Ensure it's not off-screen */
}
.text-right2 p {
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  color: #FF5B00;
  text-align: right;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  margin-right: 50px;
  left: 38rem;
  top: 340%;
  transform: translateY(-100%);
  width: 100px;
  text-transform: none;
}

.text-1 {
  width: 200px;
  opacity: 1; 
  line-height: 1.5;
  text-align: left;
  background-color: transparent;
  position: absolute;
  top: 30%;
  right: 300px;
}

.text-2 {
  width: 500px;
  line-height: 1.5;
  text-align: left;
  background-color: transparent;
  position: absolute;
  top: 70%;
  left: 300px;
}





@font-face {
  font-family: 'Inter';
  src: url('./fonts/Inter-Light.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.inter-text {
  font-family: 'Inter', sans-serif;
}




@font-face {
  font-family: 'Eros';
  src: url('./fonts/west-bold.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.eros-text {
  font-family: 'Eros', sans-serif;
}