body {
  background-color: #ff5b00;
  margin: 100px;
  overflow: hidden; /* Prevents any scrolling */
}

* {
  box-sizing: border-box;
}

.work-with-darma-2 {
  background-color: transparent;
  height: auto;
  width: 900px;
  position: absolute;
  top: 50%;
  left: 57%;
  transform: translate(-50%, -50%);
  background-color: #ff5b00;
  color: #000;
  font-family: Arial, sans-serif;
  padding-left: 80px;
  text-align: left;
  display: flex;
  flex-direction: column;
}

.form {
  max-width: 750px;
  margin: auto;
}

.input-group-2 {
  margin-bottom: 20px;
  position: relative;
}

.input-group-2 label {
  color: #000;
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 20px;
}

.input-group-2 input,
.input-group-2 textarea {
  background-color: #ff5b00;
  width: 100%;
  padding: 8px;
  border: 1px solid #000;
  border-radius: 50px;
  font-size: 16px;
  height: 30px;
}

.input-group-2 input::placeholder,
.input-group-2 textarea::placeholder {
  color: rgb(0, 0, 0);
  font-size: 12px;
}

.input-group-2 input[type="file"] {
  width: 170px;
  height: 32px;
  margin-left: -0px;
  right: 150px;
  margin-bottom: 16px;
  border: 1px solid #000;
  background-color: #000;
  color: #ff5b00;
  padding: 10px;
  border-radius: 50px;
  cursor: pointer;
  font-size: 0;
  appearance: none;
  position: absolute;
  overflow: hidden;
}

.input-group-2 input[type="file"]::before {
  background-color: #000;
  content: "Upload";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #ff5b00;
  font-size: 16px;
  pointer-events: none;
}

.input-group-2 input[type="file"]::-webkit-file-upload-button {
  display: none;
}

.input-group-2 input[type="file"]::-moz-file-upload-button {
  display: none;
}

.input-group-2 textarea {
  place-content: "kķ";
  width: 100%;
  border-radius: 10px;
  min-height: 115px;
  resize: vertical;
  padding: 8px;
}

.flex-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.input-name,
.input-phone-2,
.input-email-2 {
  width: calc(32% - 10px);
  margin-right: 10px;
}

.input-name {
  width: 250px;
}

.input-phone-2 {
  width: 100px;
}

.input-text {
  width: 100%;
  background-color: #ff5b00;
}

.file-input-wrapper {
  display: flex;
  align-items: center;
  margin-top: 14px;
}

.file-input-wrapper input[type="file"] {
  flex: 1;
}

.file-input-wrapper span {
  margin-left: 10px;
  font-size: 16px;
}

.submit-btn-2 {
  border-radius: 80%;
  position: absolute;
  margin-left: 0px;
  height: 110px;
  top: 59%;
  left: 805px;
  background-color: #000;
  color: #ff5b00;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  align-self: flex-end;
  margin-top: 1px;
}

button:hover {
  background-color: #333;
}

button span {
  font-size: 24px;
}

.input-position {
  width: 250px;
}

.input-portfolio label {
  margin-left: 19px;
}

.title-text {
  z-index: 1;
  margin-right: 20px;
  width: 200px;
  position: absolute;
  left: 300px;
  top: 110px;
  transform: translateY(80%);
}

.title-text h1 {
  line-height: 40px;
  font-size: 40px;
  font-weight: 800;
  color: #000;
  margin: 0;
  text-align: left;
}

.logo-container {
  position: absolute;
  top: 50%;
  left: -90px;
  transform: translate(50%, -50%);
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-container img {
  max-width: 100%;
  height: auto;
}

.text-choose-file span {
  z-index: 3;
  width: 200px;
  margin-left: 216px;
  margin-top: 20px;
}

.text-right-5 {
  z-index: 1;
  position: absolute;
  right: -25px;
  top: 50%;
  margin-right: 60px;
  transform: translateY(-50%);
  font-weight: bolder;
  color: #000;
  text-align: center;
  width: auto;
}

.text-right-5 p {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 20px;
  text-align: right;
}

.hiring-download-btn {
  left: 20px;
  bottom: 11px;
  width: 150px;
  height: 32px;
  background-color: #000;
  color: #ff5b00;
  padding: 10px;
  border: 1px solid #000;
  border-radius: 50px;
  cursor: pointer;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.hiring-download-btn:hover {
  background-color: #333;
}

.hiring-download-btn::before {
  content: "Download";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #ff5b00;
  font-size: 16px;
  pointer-events: none;
}

.Brief-label {
  position: relative;
  right: 260px;
  color: #000;
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  font-size: 20px;
}

.input-phone-2 input {
  position: relative;
  width: 150px;
  z-index: 1;
}

.input-email-2 input {
  margin-left: 40px;
}

.input-email-2 label {
  margin-left: 40px;
}

.portfolio{
  margin-right:10px
}


@font-face {
  font-family: 'Eros';
  src: url('./fonts/west-bold.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.eros-text {
  font-family: 'Eros', sans-serif;
}


.back-hire{
  width: 20px;
  height: 20px;
}

.back-link-hire {
  z-index: 1;
  left: 19rem;
  position: relative;
  top: 32rem;
  font-size: 20px;
  display: flex;
  align-items: center;
}

.back-link-hire img {
  margin-right: 8px; /* Adjust as needed for spacing */
  vertical-align: middle; /* Ensures the icon aligns well with the text */
}

.back-link-hire a {
  display: flex;
  align-items: center;
  text-decoration: none; /* Removes underline from the link */
  color: inherit; /* Ensures it inherits the text color */
}



@font-face {
  font-family: 'Inter';
  src: url('./fonts/Inter-Light.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.inter-text {
  font-family: 'Inter', sans-serif;
}