/* Reset and general styles */
body {
  background-color: #ff5b00;
  /* color: #ff5b00; */
  margin: 0;
  overflow: hidden; /* Prevents any scrolling */

}

* {
  box-sizing: border-box;
}

/* Container for the form */
.work-with-darma {
  background-color: transparent;
  height: auto;
  width: 900px; /* Increase width */
  position: absolute;
  top: 50%;
  left: 57%;
  transform: translate(-50%, -50%);
  background-color: #ff5b00;
  color: #000;
  font-family: Arial, sans-serif;
  padding-left: 80px;
  text-align: left;
  display: flex; /* Use flexbox for layout */
  flex-direction: column; /* Stack children vertically */
}
.work-with-darma-container{
  background-color: #ff5b00;
}
/* Form styling */
.form {
  max-width: 750px;
  margin: auto;
}

/* Input group styles */
.input-group {
  margin-bottom: 20px;
  position: relative; /* Ensure relative positioning for absolute children */
}

.input-group label {
  color: #000;
  display: block;
  margin-bottom: 5px;
  font-weight: bolder;
  font-size: 20px;
}

.input-group input,
.input-group textarea {
  background-color: #ff5b00;
  width: 100%;
  padding: 8px; /* Adjust padding */
  border: 1px solid #000;
  border-radius: 50px;
  font-size: 16px;
  height: 30px; /* Reduce height */
}

.input-group input::placeholder,
.input-group textarea::placeholder {
  color: rgb(0, 0, 0);
  font-size: 12px;
}

.input-group input[type="file"] {
  width: 180px;
  height: 32px;
  margin-left: 20px;
  margin-top: -5px;
  border: 1px solid #000;
  background-color: #000;
  color: #ff5b00;
  padding: 10px;
  border-radius: 50px;
  cursor: pointer;
  font-size: 0; /* Hide the "no file selected" text */
  appearance: none; /* Remove default appearance */
  position: absolute; /* Ensure relative positioning for pseudo-element */
  overflow: hidden; /* Hide overflowing content */
}

.input-group input[type="file"]::before {
  background-color: #000;
  content: "Choose File"; /* Text to display */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #ff5b00;
  font-size: 16px;
  pointer-events: none; /* Prevents pseudo-element from being interactive */
}

.input-group input[type="file"]::-webkit-file-upload-button {
  /* Disable default button appearance in Chrome */
  display: none;
}

.input-group input[type="file"]::-moz-file-upload-button {
  /* Disable default button appearance in Firefox */
  display: none;
}

/* Specific styles for textarea */
.input-group textarea {
  place-content: "kķ";
  width: 100%;
  border-radius: 10px;
  min-height: 115px; /* Example: Set a minimum height */
  resize: vertical; /* Allow vertical resizing */
  padding: 8px; /* Adjust padding */
}

/* Flex container for layout */
.flex-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.input-name,
.input-phone,
.input-email {
  width: calc(32% - 10px); /* Adjusted width with spacing */
  margin-right: 10px; /* Add space between inputs */
}

.input-name {
  width: 250px; /* Set specific width for name input */
}

.input-phone {
  width: 100px; /* Set specific width for phone input */
}

.input-text {
  width: 100%;
  background-color: #ff5b00;
}

/* Styles for file input wrapper */
.file-input-wrapper {
  display: flex;
  align-items: center;
  margin-top: 14px;

}

.file-input-wrapper input[type="file"] {
  flex: 1;
}

.file-input-wrapper span {
  margin-left: 10px;
  font-size: 16px;

}

/* Button styles */
button {
  color: #ff5b00;
  border-radius: 80%;
  position: absolute;
  height: 110px;
  left: 89%;
  top: 58%;
  background-color: #000;
  color: #ff5b00;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  align-self: flex-end; /* Align button to the end of the flex container */
  margin-top: 1px;
}

button:hover {
  background-color: #333;
}

button span {
  font-size: 24px;
}

.input-position {
  width: 250px;
}

.input-portfolio label {
  margin-left: 30px; /* Adjusted margin */
}

