html, body {
  height: 100%;
  margin: 0;
}

.section3-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #000;
  color: #fff;
  text-align: center;
  position: relative;
  max-width: 1920px;
  max-height: 1080px;
}

.clossiom-container {
  position: relative;
  margin-top: 55px;
}

.clossiom {
  left: 10px;
  z-index: 0;
  position: relative;
}

.clossiom img {
  opacity: 0;
  z-index: 0;
  margin-top: 55px;
  right: 34.25%;
  height: 400px;
  width: 550px;
}

.shadow_3 {
  z-index: 1;
  background: transparent;
  position: absolute;
  top: 85%;
  left: 60%;
  transform: translateX(-50%);
  width: 700px;
  height: 120px;
  text-align: center;
}

.shadow_3 img {
  background: transparent;
  width: 100%;
  height: 50%;
}

.parallax-container {
  display: flex;
  width: auto;
  background: transparent;
  align-items: center;
  justify-content: space-between;
  justify-items: center;
  position: relative;
}

.girl{
  bottom:-11rem;
  left: -5rem;
}

.dude{
  bottom: -14rem;
}

.girl, .dude {
  background: transparent;
  transition: transform 0.5s ease;
  position: absolute;
}

.girl img, .dude img {
  background: transparent;
  transition: transform 0.05s ease;
  background-color: transparent;
}

.girl img {
  z-index: 1000;
  position: relative;
  background: transparent;
  height: 430px;
  width: 300px;
  top: 190%;
  right: 130%;
}

.dude img {
  z-index: 1000;

  position: relative;
  background: transparent;
  height: 500px;
  width: 400px;
    left: 30%;
}

.top-text {
  line-height: 1.5rem;
  background: transparent;
  display: flex;
  justify-content: space-between;
  width: auto;
  position: absolute;
  top: 8%;
  left: 60%;
  transform: translateX(-50%);
}

.text-item {
  margin: 0 100px;
  font-size: 1.5rem;
}

.text-item-1 {
  background: transparent;
  width: 300px;
  font-weight: 800;
  font-size: 80px;
  font-weight: bolder;
  font-weight: 500;
  line-height: 1;
}
.text-item-3  {
  margin-right: 200px;
}
.text-item-2 p{
  margin-left: 20%;
}
.text-item-2, .text-item-3 {
  width: 220px;
  background: transparent;
  margin-left: -30px;
  text-align: left;
  font-size: medium;
  font-weight: lighter;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.group-logo3 {
  position: absolute;
  top: 47%;
  right: 880px;
}
.animated-letter-section3 {
  text-align: right;
}

.text-right-3 {
  font-weight: 900;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 15px;
  color: #FF5B00;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  margin-right: 50px;
  right:  -27rem;
  top: 50%;
  transform: translateY(-50%);
}

.menu-icon-3 {
  position: absolute;
  top: -5rem;
  left: 60px;
  width: 170px;
  height: auto;
  cursor: pointer;
  z-index: 1000;
}

.menu-links-3 {
  z-index: 1001;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 8%;
  transform: translate(-50%, -50%);
}

.menu-link-3 {
  position: absolute;
  color: #000000;
  font-size: 20px;
  text-decoration: none;
  pointer-events: auto;
}

.menu-link:hover {
  text-decoration: underline;
}

.home-link-3 {
  top: 30px;
  left: 120px;
  z-index: 1;
}

.work-link-3 {
  margin-left: 130px;
  margin-bottom: 40px;
  top: -10px;
}

.about-link-3 {
  top: -60px;
  left: 120px;
}

@font-face {
  font-family: 'Eros';
  src: url('./fonts/west-bold.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.eros-text {
  font-family: 'Eros', sans-serif;
}


@font-face {
  font-family: 'Inter';
  src: url('./fonts/Inter-Light.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.inter-text {
  font-family: 'Inter', sans-serif;
}

.container{
  margin-top: 10rem;
  top: -120%;
  position: absolute;
  width: 210%;
  height: 310%;
  right: -16%;
}