.starter-section {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #000000;
  }
  
  .logogif {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .giflogo {
    max-width: 15%;
    height: auto;
  }
  
  .text-right-starter,
  .text-left-starter {
    font-size:5rem;
    color: white;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    line-height: 0.6;
  }
  
  .text-right-starter {
    right: 60px;
  }
  
  .text-left-starter {
    left: 60px;
  }
  
  .eros-text {
    font-family: 'Eros', sans-serif;
  }
  
  @font-face {
    font-family: 'Eros';
    src: url('./fonts/west-bold.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }
  
  /* Media Queries */
  @media (max-width: 1200px) {
    .text-right-starter,
    .text-left-starter {
      font-size: 3rem;
      justify-content: end;

    }
    .text-left-starter{
        margin-left: 100px;
    }
    .text-right-starter{
        margin-right: 100px;
    }
  }
  
  @media (max-width: 992px) {
    .text-right-starter,
    .text-left-starter {
      font-size: 3rem;
    }
  }
  
  @media (max-width: 768px) {
    .text-right-starter,
    .text-left-starter {
      font-size: 2.5rem;
      line-height: 1;
    }
  
    .text-right-starter {
      right: 10px;
    }
  
    .text-left-starter {
      left: 10px;
    }
  
    .giflogo {
      max-width: 20%;
    }
  }
  
  @media (max-width: 576px) {
    .text-right-starter,
    .text-left-starter {
      font-size: 2rem;
      line-height: 1.2;
      top: 50%;
    }
  
    .giflogo {
      max-width: 30%;
    }
  }
  